.List {
  width: 400px;
  display: flex;
  flex-direction: column;
}
.Item {
  display: flex;
  align-items: center;
}
.Item:not(:last-child) {
  margin-bottom: 10px;
}
.Contact {
  font-size: 16px;
}
.Contact:not(:last-child) {
  margin-right: 5px;
}
.Button {
  margin-left: auto;
  padding: 5px 10px;
  background-color: transparent;
  border: 1px solid #d8d8d8;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 600;
}
.Button:hover,
.Button:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  background-color: #5ab373;
  border: 1px solid transparent;
  color: #fff;
}
